*, ::after, ::befor {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }


html {
    font-size: 62.5%; }

body {
    font-family: 'Roboto';
    background: #41442C;
    color: #fff;
    padding: 40px 92px; }
a {
    text-decoration: none;
    color: #F2F1EE; }

.container {
    max-width: 1366px; }
.header {
    display: flex;
    justify-content: space-between; }
.logo {
    display: flex;
    align-items: center;
    width: 18rem;
    height: 5rem; }
.menu__list {
    display: flex;
    gap: 2rem;
    font-size: 1.6rem; }
.menu__list-item {
    list-style-type: none; }

.menu__list-link {}


.menu__btn {
    display: none; }

.lang {
    display: flex;
    align-items: center;
    font-size: 1.6rem; }
.reg {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #2F2C22;
    background: #EBD7B2;
    width: 18rem;
    font-size: 1.6rem; }
